export const Result = (props) => {
    const { countryData } = props;
    return (
        <div className="results-container">
        <div>
            <p>: <span>{countryData.date.slice(0, 10)}</span></p>
            <p>: <span>{countryData.newConfirmed.toLocaleString()}</span> </p>
            <p>: <span>{countryData.totalConfirmed.toLocaleString()}</span> </p>
            <p>: <span>{countryData.newRecovered.toLocaleString()}</span> </p>
            <p>: <span>{countryData.totalRecovered.toLocaleString()}</span> </p>
        </div>
        </div>
    );
};