export const Card = (props) => {
    const { allCountriesData } = props;
    return (
        <div className="card-container">
        {allCountriesData.map((singleData, index) => 
            <div key={index} className="card">
                <div>
                <h2>{singleData.Country}</h2>
                <p>: <span>{singleData.NewConfirmed.toLocaleString()}</span> </p>
                <p>: <span>{singleData.TotalConfirmed.toLocaleString()}</span> </p>
                <p>: <span>{singleData.NewDeaths.toLocaleString()}</span> </p>
                </div>
            </div>
        )}
        </div>
    );
};